.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    width: 100%;
    &__icon{
        object-fit: contain;
        height: 100px;
        margin-left: 20px;
    }
    &__center{
        display: flex;
        flex: 1;
        align-items: center;
        max-width: fit-content;
        padding: 10px;
        height: 30px;
        border: 1px solid lightgray;
        border-radius: 999px;
        &>input{
            border: none;
            width: 250px;
            padding: 10px;
            outline-width: 0;
        }
    }
    &__right{
        display: flex;
        align-items: center;
        justify-self: space-between;
        width: 15vw;
        margin-right: 80px;
    }
}